.footer
  padding: 41px 0 40px 0
  background: $color-white
  box-shadow: 0px 1px 44px rgba(0, 0, 0, 0.12)
  &__wrapper
    display: flex
    justify-content: space-between
  &__logo
    display: block
    width: 170px
    height: 147px
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
  &__search
    margin-bottom: 54px
  &__info
    display: flex
    flex-direction: column
    justify-content: space-between
    width: 152px
    height: 127px
  &__link
    display: block
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
    &:hover
      color: $color-dark-grey

.footer-list
  list-style: none
  font-size: 16px
  line-height: 20px
  max-width: 216px
  height: 176px
  &__item
    margin-bottom: 32px
    &_empty
      height: 18px
      margin-bottom: 0
    &_disabled
      display: block
  &__link
    transition: all 0.3s
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
    &:hover
      color: $color-dark-grey

.social-group
  display: flex
  align-items: center
  &_desktop
    display: flex
  &_mobile
    display: none
  &__link
    display: flex
    align-items: center
    justify-content: center
    width: 37px
    height: 37px
    padding: 6px
    border-radius: 50%
    background: $color_light-grey
    fill: $color-dark-grey
    &:not(:last-child)
      margin-right: 20px
      padding: 6px 9px 6px 3px
    &:hover
      fill: #CD3B31
      background: $color-grey
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
