.about-us
  padding-bottom: 40px
  &__wrapper
    display: flex
    justify-content: space-between
    align-items: center
  &__img-block
    width: 1163px
    height: 637px
  &__text-block
    width: 566px
  &__title
    @include section-title-one

  &__text:not(:last-child)
    margin-bottom: 34px

.presentation
  display: flex
  margin-bottom: 65px
  &__btn
    @include no-btn
    display: flex
    align-items: center
    font-size: 24px
    line-height: 29px
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
    &:hover
      color: $color-dark-grey
      .presentation__icon
        fill: $color-accent
  &__icon
    width: 32px
    height: 32px
    padding: 5px
    margin-left: 10px
    fill: $color-main

.advantages
  margin-bottom: 52px
  &_l
    display: none
  &__title
    margin-bottom: 34px
  &__list
    list-style: none
  &__item
    @include marker-text
