@media  only screen and (max-width:1800px)
  .container
    max-width: 1600px

  .about-us
    &__img-block
      width: 1020px
    &__text-block
      width: 560px

  .partners-slide
    width: 941px
    &__item
      width: 293px
  .partners
    &__text-block
      width: 630px

@media  only screen and (max-width:1640px)
  .container
    max-width: 1500px
  .about-us
    &__img-block
      width: 916px
      height: 580px

  .presentation
    margin-bottom: 24px
  .partners
    &__text-block
      width: 540px

  .list-footer
    column-count: 3

  .footer-list
    &__item
      margin-bottom: 24px

@media  only screen and (max-width:1540px)
  .container
    max-width: 1400px

  .header
    &__list
      width: 160px

  .nav-menu
    width: 123px
    margin-right: 56px

  .about-us
    &__text-block
      width: 455px
    &__text:not(:last-child)
      margin-bottom: 24px
  .advantages
    &__title
      font-size: 20px
      line-height: 25px
      margin-bottom: 24px

  .partners-slide
    width: 627px
    &__item
      width: 306px
  .partners__text-block
    width: 735px

@media only screen and (max-width:1440px)
  .container
    max-width: 1200px

  .header
    &__search
      margin-right: 80px
    &__list
      margin-right: 80px

  .menu
    overflow: scroll
    -ms-overflow-style: none
    scrollbar-width: none
    &::-webkit-scrollbar
      width: 0
      height: 0
    &__wrapper
      flex-wrap: wrap

  .about-us
    &__wrapper
      flex-wrap: wrap
    &__img-block
      width: 720px
      height: 400px
    &__text-block
      display: flex
      flex-direction: column
      justify-content: space-between
      height: 350px

    .advantages
      padding-top: 24px
      &__title
        text-align: center
      &_xl
        display: none
      &_l
        display: block
        margin-left: auto
        width: 100%
        margin-bottom: 0

  .partners
    &__text-block
      width: 560px

  .gallery
    &__wrapper
      grid-template-rows: 465px 290px
      gap: 20px
    &__num
      font-size: 32px
      line-height: 40px
    &__text
      margin-bottom: 12px

  .footer
    &__logo
      width: 135px
      height: 120px

  .footer-list
    font-size: 14px
    line-height: 16px
    &__item
      margin-bottom: 15px

@media  only screen and (max-width:1240px)
  .container
    max-width: 1100px

  .about-us
    &__img-block
      width: 620px
      height: 350px
    &__text-block
      height: 320px

  .partners-slide
    width: 595px
    &__wrapper
      height: 145px
    &__item
      width: 290px
  .partners
    &__title
      font-size: 36px
      line-height: 54px
    &__text-block
      width: 500px

  .footer-list
    max-width: 116px

@media  only screen and (max-width:1140px)
  .container
    max-width: 1000px

  .header
    &__list
      margin-right: 60px
      width: 136px
    &__search
      margin-right: 60px

  .about-us
    &__img-block
      width: 515px
      height: 315px

  .partners
    &__wrapper
      flex-direction: column
    &__text-block
      width: 100%

  .partners-slide
    width: 100%
    height: auto
    margin-bottom: 32px
    &__wrapper
      margin-bottom: 32px

  .gallery
    &__wrapper
      grid-template-rows: 385px 265px
    &__num
      font-size: 30px
      line-height: 38px

@media  only screen and (max-width:1024px)
  .container
    max-width: 960px

  .header
    &__list
      display: none

  .about-us
    &__img-block
      width: 484px
      height: 295px
    &__text-block
      height: 295px
  .gallery
    &__wrapper
      grid-template-rows: 90px 200px 290px
    &__info
      grid-row: 1/2
      grid-column: 1/9
      flex-direction: row
    &__photo
      &_l
        grid-row: 3/4
        grid-column: 8/-1
      &_s
        grid-row: 3/4
        grid-column: 4/8
      &_m
        grid-row: 3/4
        grid-column: 1/4
      &_xxl
        grid-row: 2/3
        grid-column: 1/9
      &_xl
        grid-row: 1/3
        grid-column: 9/-1

  .footer
    &__search
      margin-bottom: 40px
  .footer-list
    max-width: 200px
    height: auto
    &__item
      &_disabled
        display: none
    &__info
      height: 120px

  .list-footer
    column-count: 1

@media  only screen and (max-width:992px)
  .container
    max-width: 750px

  .menu
    &__wrapper
      justify-content: space-between

  .about-us
    &__img-block
      width: 365px
      height: 220px
    &__text-block
      width: 365px
      height: 260px
    &__title
      font-size: 32px
      line-height: 46px
      margin-bottom: 15px
    &__text:not(:last-child)
      margin-bottom: 16px

  .presentation
    margin-bottom: 16px
    &__btn
      font-size: 20px

  .partners-slide
    &__wrapper
      height: 175px

  .gallery
    &__wrapper
      grid-template-columns: repeat(8, 1fr)
      grid-template-rows: 90px 250px 200px 200px
    &__info
      grid-row: 1/2
      grid-column: 1/-1
    &__photo
      &_l
        grid-row: 4/5
        grid-column: 1/6
      &_s
        grid-row: 4/5
        grid-column: 6/-1
      &_m
        grid-row: 3/4
        grid-column: 4/-1
      &_xxl
        grid-row: 2/3
        grid-column: 1/-1
      &_xl
        grid-row: 3/4
        grid-column: 1/4

  .footer
    &__wrapper
      flex-wrap: wrap
    &__info
      flex-direction: row
      align-items: flex-end
      height: 50px
      width: 100%
    &__link:last-child
      font-size: 18px
      line-height: 23px

  .social-group
    justify-content: flex-end

@media  only screen and (max-width:768px)
  .container
    max-width: 93.75%
    min-width: 300px

  .header
    &__logo
      width: 69px
      height: 64px
    &__search
      display: none

  .hours
    margin-right: 29px
    &__text
      display: none

  .header
    &__menu
      top: 80px

  .menu
    &__wrapper
      flex-direction: column

  .menu-list
    margin-right: 0
    padding: 0
    max-width: 100%
    text-align: center
    &:not(:last-child)
      margin-right: 0
    &__submenu
      display: none
    &__link-title
      margin-bottom: 48px
  .nav-menu
    text-align: center
    width: 100%
    margin-right: 0
    padding: 0
    border: none
    order: 10

  .about-us
    &__img-block
      width: 100%
      height: 290px
    &__text-block
      width: 100%
      height: auto
      margin-bottom: 24px
      order: -1
    &__title
      text-align: center
      bottom: 24px
    &__text:not(:last-child)
      margin-bottom: 32px

  .presentation
    margin-bottom: 32px
    &__btn
      margin: 0 auto
    &__icon
      fill: $color-accent

  .list-wrap
    column-count: 1

  .partners-slide
    &__wrapper
      height: 145px
    &__item
      width: 300px
  .partners
    &__title
      text-align: center

  .gallery
    &__wrapper
      grid-template-columns: repeat(4, 1fr)
      grid-template-rows: 198px repeat(3, 255px)
    &__info
      grid-row: 1/2
      grid-column: 1/-1
      display: grid
      grid-template-columns: repeat(4, 1fr)
      grid-template-rows: repeat(2, 90px)
      gap: 16px
    &__text, &__num
      text-align: center
    &__info-group
      display: flex
      flex-direction: column
      &_one
        grid-row: 1/2
        grid-column: 1/3
      &_two
        grid-row: 1/2
        grid-column: 3/5
      &_three
        grid-row: 2/3
        grid-column: 1/3
      &_four
        grid-row: 2/3
        grid-column: 3/5

    &__photo
      &_l
        grid-row: 4/5
        grid-column: 1/3
      &_s
        grid-row: 4/5
        grid-column: 3/-1
      &_m
        grid-row: 3/4
        grid-column: 3/-1
      &_xxl
        grid-row: 2/3
        grid-column: 1/-1
      &_xl
        grid-row: 3/4
        grid-column: 1/3

  .footer
    padding: 21px 0 23px 0
    &__wrapper
      flex-direction: column
      align-items: center
      flex-wrap: no-wrap
    &__logo
      margin-bottom: 22px
      order: 0
    &__group
      order: 1
      margin-bottom: 24px
      width: 100%
    &__search
      width: 100%
      margin-bottom: 0
    &__info
      order: 3
      flex-direction: column
      align-items: center
      height: auto
      margin-bottom: 24px
    &__link,  &__hours
      margin-bottom: 21px
      text-align: center
      width: 152px
    &__link:last-child
      font-weight: 700
      font-size: 24px
      line-height: 29px
      text-align: center
      width: 100%

  .social-group
    &__link
      width: 47px
      height: 47px
    &__link:not(:last-child)
      margin-right: 30px
    &_desktop
      display: none
    &_mobile
      display: flex
      order: 10

  .footer-list
    width: 100%
    text-align: center
    margin-bottom: 24px

  .list-footer
    order: 2

@media  only screen and (max-width:480px)
  .gallery
    &__wrapper
      grid-template-rows: 409px 206px repeat(2, 160px)
    &__info
      grid-template-rows: repeat(2, 90px)

    &__info-group
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      &_one
        grid-row: 1/2
        grid-column: 1/-1
      &_two
        grid-row: 2/3
        grid-column: 1/-1
      &_three
        grid-row: 3/4
        grid-column: 1/-1
      &_four
        grid-row: 4/5
        grid-column: 1/-1
