.header
  position: fixed
  top: 0
  right: 0
  left: 0
  padding: 7px 0 9px 0
  background: $color-white
  z-index: 100
  box-shadow: 0px 4px 26px rgba(0, 0, 0, 0.09)// прискролле

  &__wrapper
    display: flex
    align-items: center
  &__logo
    display: block
    width: 91px
    height: 84px
    margin-right: auto
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
  &__hours
    margin-right: 86px
  &__list
    display: flex
    justify-content: space-between
    width: 202px
    list-style: none
    margin-right: 97px
  &__link
    position: relative
    transition: all 0.3s
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
    &:hover
      color: $color-dark-grey
  &__menu
    display: none
    position: absolute
    top: 100px
    right: 0
    bottom: 0
    left: 0
    background: $color-white
    width: 100%
    height: 0
    opacity: 0
    transition: all 0.3s

    &_ative
      display: block
      height: 100vh
      opacity: 1
      transition: all 0.3s

  &__search
    margin-right: 177px

.hours
  display: flex
  color: $color-dark-grey
  margin-right: 5%
  cursor: default
  &__text:not(:last-child)
    margin-right: 6px

.search
  position: relative
  width: 268px
  height: 40.43px

  &__input
    width: 100%
    height: 100%
    padding: 12.77px 35px 11.7px 14px
    border: none
    border-radius: 88px
    font-size: 12px
    line-height: 15px
    box-shadow: 2px 4px 11px rgba(0, 0, 0, 0.17)
    border-radius: 88px
    overflow: hidden
    transition: all 0.3s
    &::placeholder
      color: $color-grey
    &:hover
      transition: all 0.3s
      box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.17)
    &:focus
      outline: 0.5px solid $color-light-grey

  &__icon
    position: absolute
    top: 50%
    right: 15px
    width: 20px
    transform: translateY(-50%)
    padding: 2.5px
    height: 21px

.menu-btn
  position: relative
  @include no-btn
  width: 33px
  height: 25px
  &::after
    content: ''
    position: absolute
    bottom: 0
    display: block
    width: 33px
    height: 4px
    background: $color-accent
    border-radius: 17px
    transition: all 0.3s
  &::before
    content: ''
    position: absolute
    top: 0
    display: block
    width: 33px
    height: 4px
    background: $color-accent
    border-radius: 17px
    transition: all 0.3s
  &:hover
    transition: all 0.3s
    .menu-btn__line
      transition: all 0.3s
      width: 100%

  &:focus-visible
    outline: 1px dashed black
    outline-offset: 4px

  &_open-menu
    .menu-btn__line
      transition: all 0.3s
      background: transparent

    &::before
      top: 45%
      left: 0
      transform: rotate(-45deg)
      transition: all 0.3s

    &::after
      top: 45%
      left: 0
      transform: rotate(45deg)
      transition: all 0.3s

  &__line
    position: absolute
    top: 50%
    right: 0
    transform: translateY(-50%)
    display: block
    width: 18px
    height: 4px
    background: $color-accent
    border-radius: 17px
    transition: all 0.3s

.menu
  padding-bottom: 41px
  &__wrapper
    display: flex
    justify-content: flex-end
  &__link
    &:hover
      color: $color-dark-grey
      transition: all 0.3s
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px

.nav-menu
  padding-top: 31px
  list-style: none
  margin-right: 149px
  width: 153px
  border-right: 1px solid $color-grey
  &__item
    font-size: 20px
    line-height: 24px
    margin-bottom: 48px
  &__link
    transition: all 0.5s
    color: inherit
    &:hover
      color: $color-dark-grey
      transition: all 0.3s
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px

.menu-list
  padding-top: 19px
  list-style: none
  min-width: 196px
  &:not(:last-child)
    margin-right: 30px
  &__item
    font-size: 20px
    line-height: 24px
  &__link-title
    display: block
    font-weight: 700
    font-size: 24px
    line-height: 29px
    margin-bottom: 34px
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px

.sub-menu
  list-style: none
  font-size: 16px
  line-height: 40px
  &__item
    margin-bottom: 16px
  &__link
    &:hover
      color: $color-dark-grey
