.gallery
  padding: 40px 0 60px 0
  &__wrapper
    display: grid
    grid-template-columns: repeat(12, 1fr)
    grid-template-rows: 508px 359px
    gap: 30px
  &__info
    grid-row: 1/2
    grid-column: 1/3
    display: flex
    flex-direction: column
    justify-content: space-between

  &__photo
    &_l
      grid-row: 2/3
      grid-column: 8/-1
    &_s
      grid-row: 2/3
      grid-column: 4/8
    &_m
      grid-row: 2/3
      grid-column: 1/4
    &_xxl
      grid-row: 1/2
      grid-column: 7/-1
    &_xl
      grid-row: 1/2
      grid-column: 3/7

  &__text
    color: $color-dark-grey
    font-weight: 700
    font-size: 16px
    line-height: 20px
    margin-bottom: 17px
  &__num
    font-weight: 700
    font-size: 36px
    line-height: 44px
