.partners
  padding: 40px 0
  &__title
    @include section-title-one
  &__wrapper
    display: flex
    justify-content: space-between
  &__text-block
    width: 716px
  &__text
    @include marker-text

.partners-slide
  display: flex
  flex-direction: column
  justify-content: space-between
  width: 1012px
  height: 206px
  margin-left: 0
  &__wrapper
    height: 153px
    margin-bottom: 16px
  &__item
    width: 306px
    background: #f006a2
  &__control
    display: flex
    justify-content: center
    align-items: center
    height: 24px
  &__btn
    width: 42px
    height: 15px
    cursor: pointer
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px
  &__pagination
    display: flex
    justify-content: center
    align-items: center
    min-width: 47px
    margin: 0 8px

.swiper-pagination-bullet, .swiper-pagination-bullet-active
  width: 6px
  height: 6px
  margin: 0 6px
  border-radius: 50%
  background: rgba(107, 112, 115, 0.5)
  &:focus-visible
    outline: 1px dashed $color-accent
    outline-offset: 4px
.swiper-pagination-bullet-active
  width: 9px
  height: 9px
  background: $color-dark-grey
