.breadcrumb
  margin-top: 100px
  padding: 29px 0 20px 0
  &__list
    list-style: none
    display: flex
  &__item
    color: $color-dark-grey
    &:not(:last-child)::after
      content: '>'
      color: $color-grey
      margin-right: 8px
  &__link
    color: $color-grey
    padding-right: 8px
    &:focus-visible
      outline: 1px dashed $color-accent
      outline-offset: 4px

    &:hover
      color: $color-dark-grey
      transition: all 0.3s
