body
  font-family: Calibri, Candara, Segoe, Segoe UI, Optima, Arial, sans-serif
  font-weight: 400
  font-size: 14px
  line-height: 17px
  color: $color-main
  background: $color-white

.container
  max-width: 1760px
  margin: 0 auto

.list-wrap
  list-style-position: inside
  column-count: 2
.list-footer
  list-style-position: inside
  column-count: 4
